'use client';
import React, { useState, useEffect, useMemo } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { ChevronRight, ChevronLeft, X, Plus } from 'lucide-react';

const ClassTimeDashboard = ({ classId }) => {
  const [days, setDays] = useState([]);
  const [times, setTimes] = useState([]);
  const [selectedDay, setSelectedDay] = useState('');
  const [availableTimesSearch, setAvailableTimesSearch] = useState('');
  const [dayTimesSearch, setDayTimesSearch] = useState('');
  const [dayTimes, setDayTimes] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Fetch days and times from the API
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const [daysResponse, timesResponse] = await Promise.all([
          fetch('http://localhost:9921/days').then(res => res.json()),
          fetch('http://localhost:9921/timeslots').then(res => res.json()),
        ]);
        
        const safeDays = daysResponse.map((day, index) => ({
          ...day,
          uniqueId: day.id ? `day-${day.id}` : `day-${index}`,
          name: day.name || `Day ${index + 1}`
        }));

        const safeTimes = timesResponse.map((time, index) => ({
          ...time,
          uniqueId: time.id ? `time-${time.id}` : `time-${index}`,
          timeCode: time.timeCode || `TIME-${index + 1}`,
          name: time.name || `Unnamed Time ${index + 1}`,
          duration: time.duration || 'Unknown Duration'
        }));
        
        setDays(safeDays);
        setTimes(safeTimes);
        
        if (safeDays.length > 0) {
          setSelectedDay(safeDays[0].uniqueId);
        }
      } catch (err) {
        console.error('Detailed Fetch Error:', err);
        setError(`Failed to load data: ${err.message}`);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  // Filtered available times
  const filteredAvailableTimes = useMemo(() => {
    const currentDayTimes = dayTimes[selectedDay] || [];
    return times.filter(
      (time) =>
        time.uniqueId && 
        !currentDayTimes.some((t) => t.uniqueId === time.uniqueId) &&
        time.timeCode.toLowerCase().includes(availableTimesSearch.toLowerCase())
    );
  }, [availableTimesSearch, selectedDay, dayTimes, times]);

  // Filtered times for the selected day
  const filteredDayTimes = useMemo(() => {
    return (dayTimes[selectedDay] || []).filter(
      (time) =>
        time.uniqueId && 
        time.timeCode.toLowerCase().includes(dayTimesSearch.toLowerCase())
    );
  }, [dayTimesSearch, selectedDay, dayTimes]);

  // Add time to the selected day
  const addTimeToDayHandler = (time) => {
    setDayTimes((prev) => ({
      ...prev,
      [selectedDay]: [...(prev[selectedDay] || []), time],
    }));
  };

  // Remove time from the selected day
  const removeTimeFromDayHandler = (timeToRemove) => {
    setDayTimes((prev) => ({
      ...prev,
      [selectedDay]: prev[selectedDay]?.filter((time) => time.uniqueId !== timeToRemove.uniqueId) || [],
    }));
  };

  // Submit selected times
  const submitSelectedTimes = async () => {
    try {
      const dayId = selectedDay.replace('day-', '');
      
      // Extract time IDs, removing the 'time-' prefix
      const timeIds = (dayTimes[selectedDay] || []).map(time => 
        time.uniqueId.replace('time-', '')
      );

      // Submit times sequentially
      for (const timeId of timeIds) {
        const response = await fetch('http://localhost:9921/classSchedules', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            classId,
            dayId,
            timeId
          })
        });

        if (!response.ok) {
          throw new Error('Failed to submit schedule');
        }
      }

      // Clear the selected times after successful submission
      setDayTimes(prev => ({
        ...prev,
        [selectedDay]: []
      }));

      // Optional: Add a success notification
      alert('Times submitted successfully!');
    } catch (err) {
      console.error('Submission Error:', err);
      alert('Failed to submit times');
    }
  };

  // Render loading state
  if (loading) return (
    <div className="flex justify-center items-center h-screen">
      <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-blue-500"></div>
    </div>
  );

  // Render error state
  if (error) return (
    <Card className="max-w-md mx-auto mt-10 border-red-500">
      <CardHeader className="bg-red-50">
        <CardTitle className="text-red-600">Error</CardTitle>
      </CardHeader>
      <CardContent className="text-red-500 p-4">{error}</CardContent>
    </Card>
  );

  // Render no data state
  if (days.length === 0) {
    return (
      <Card className="max-w-md mx-auto mt-10">
        <CardHeader>
          <CardTitle>No Data Available</CardTitle>
        </CardHeader>
        <CardContent className="text-gray-500 text-center">
          No days are currently available for scheduling.
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="container mx-auto p-6 max-w-4xl">
      <Card className="w-full">
        <CardHeader className="bg-blue-50 border-b">
          <CardTitle className="text-2xl font-bold text-blue-800">
            Class Time Dashboard
          </CardTitle>
          <p className="text-gray-600">Manage and schedule class times</p>
        </CardHeader>
        
        <CardContent className="p-6">
          <div className="mb-6">
            <label htmlFor="day-select" className="block text-sm font-medium text-gray-700 mb-2">
              Select Day
            </label>
            <Select 
              value={selectedDay}
              onValueChange={(value) => setSelectedDay(value)}
            >
              <SelectTrigger>
                <SelectValue placeholder="Select a day" />
              </SelectTrigger>
              <SelectContent>
                {days.map((day) => (
                  <SelectItem 
                    key={day.uniqueId} 
                    value={day.uniqueId}
                  >
                    {day.name}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div className="grid md:grid-cols-2 gap-6">
            {/* Available Times Column */}
            <Card className="border-blue-100">
              <CardHeader className="bg-blue-50 border-b">
                <CardTitle className="text-lg text-blue-800">
                  Available Times
                </CardTitle>
              </CardHeader>
              <CardContent className="p-0">
                <div className="p-4 border-b">
                  <Input
                    type="text"
                    placeholder="Search available times"
                    value={availableTimesSearch}
                    onChange={(e) => setAvailableTimesSearch(e.target.value)}
                    className="w-full"
                  />
                </div>
                <div className="max-h-96 overflow-y-auto">
                  {filteredAvailableTimes.map((time) => (
                    <div
                      key={time.uniqueId}
                      className="flex justify-between items-center p-4 border-b hover:bg-blue-50 transition-colors"
                    >
                      <div>
                        <p className="font-medium text-blue-800">{time.timeCode}</p>
                        <p className="text-sm text-gray-600">{time.name}</p>
                        <p className="text-xs text-gray-400">{time.duration}</p>
                      </div>
                      <Button 
                        variant="outline" 
                        size="icon" 
                        onClick={() => addTimeToDayHandler(time)}
                        className="text-green-600 hover:text-green-800"
                      >
                        <Plus className="h-5 w-5" />
                      </Button>
                    </div>
                  ))}
                  {filteredAvailableTimes.length === 0 && (
                    <p className="text-center text-gray-500 p-4">No times found</p>
                  )}
                </div>
              </CardContent>
            </Card>

            {/* Day Times Column */}
            <Card className="border-red-100">
              <CardHeader className="bg-red-50 border-b">
                <CardTitle className="text-lg text-red-800">
                  Times for Selected Day
                </CardTitle>
              </CardHeader>
              <CardContent className="p-0">
                <div className="p-4 border-b">
                  <Input
                    type="text"
                    placeholder="Search added times"
                    value={dayTimesSearch}
                    onChange={(e) => setDayTimesSearch(e.target.value)}
                    className="w-full"
                  />
                </div>
                <div className="max-h-96 overflow-y-auto">
                  {filteredDayTimes.map((time) => (
                    <div
                      key={time.uniqueId}
                      className="flex justify-between items-center p-4 border-b hover:bg-red-50 transition-colors"
                    >
                      <div>
                        <p className="font-medium text-red-800">{time.timeCode}</p>
                        <p className="text-sm text-gray-600">{time.name}</p>
                        <p className="text-xs text-gray-400">{time.duration}</p>
                      </div>
                      <Button 
                        variant="outline" 
                        size="icon" 
                        onClick={() => removeTimeFromDayHandler(time)}
                        className="text-red-600 hover:text-red-800"
                      >
                        <X className="h-5 w-5" />
                      </Button>
                    </div>
                  ))}
                  {filteredDayTimes.length === 0 && (
                    <p className="text-center text-gray-500 p-4">No times added</p>
                  )}
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Submit Button */}
          <div className="mt-6 text-center">
            <Button
              onClick={submitSelectedTimes}
              disabled={!(dayTimes[selectedDay] && dayTimes[selectedDay].length > 0)}
              className="w-full md:w-auto"
            >
              Submit Selected Times
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default ClassTimeDashboard;